




















































































































































































import Vue from "vue";
import Component from "vue-class-component";
import axios from "axios";
import { Prop } from "vue-property-decorator";
import * as indexApi from "../../api/indexApi";
import corpperlabel from './corpperlabel'

@Component({
  props: {},
  components: {
    corpperlabel
    // categoryView,
  }
})
export default class AddGoods extends Vue {
  @Prop({ required: false })
  prescriptionEnums: any;
  @Prop({ required: false })
  operationType: any;
  @Prop({ required: false })
    row

detailInfo= [
 {
   title:'',
   children:[
      {name:'',key:'memberName'},
      {name:'',key:'patientSex'},
      {name:'',key:'patientAge'},
      {name:'',key:'consigneeAddress'},
      {name:'',key:'memberPhone'},
      {name:'',key:'memberIdcard'},
   ]
 },
 {
   title:'',
   children:[
      {name:'',key:'docterName'},
      {name:'',key:'doctorMobile'},
      {name:'',key:'hospitalName'},
      {name:'',key:'docterDept'},
      
   ]
 }
  
]

model = false

  preIndex = 0;
  handleStatus(status) {
    switch (status) {
      case "NOT_TRANSLATED_PRESCRIPTION":
        return "";
      case "ALREADY_TRANSLATED_PRESCRIPTION":
        return "";
      case "FAIL_TRANSLATED_PRESCRIPTION":
        return "";
      case "REJECT_TRANSLATED_PRESCRIPTION":
        return "";
      case "ALREADY_AUDIT_PRESCRIPTION":
        return "";
      case "FAIL_AUDIT_PRESCRIPTION":
        return "";
      case "REJECT_AUDIT_PRESCRIPTION":
        if (this.$route.path == "/audit") {
          return "";
        } else {
          return "";
        }
      default:
        return "";
    }
  }
  
  preImageList = [];

getPrePic(presId) {
    indexApi.getPrePic({ preId: presId }).then(res => {
      if (res["retCode"]) {
        console.log('',res.data)
        this.preImageList = res.data;
         if(res.data.length>0){
          let a:any =    this.$refs.cropper
     a.changePreImageUrl(0);
        }
      } else {
        if(!res['islogin']){this.$alert(res["message"]);}
        console.error("");
      }
    });

  }

  preDrugList = [];
  queryPresDrugback(presId) {

    if(this.row.presState =='REJECT_AUDIT_PRESCRIPTION' || this.row.presState =='REJECT_TRANSLATED_PRESCRIPTION'){
    indexApi.queryPresDrug({ preId: presId }).then(res => {
      if (res["retCode"]) {
        this.preDrugList = res.data.data;
      } else {
        if (!res["islogin"]) {
          this.$alert(res["message"]);
        }
        console.error("");
      }
    });
    }else{
       indexApi
      .queryPresDrugback({
        preId: presId
      })
      .then(res => {
        if (res["retCode"]) {
          console.log("id", res);
          this.preDrugList = res.data;
        } else {
          if(!res['islogin']){this.$alert(res["message"]);}
          console.error("");
        }
      }); 
    }
  


  }

getInfo(){
    this.model = true;
    setTimeout(() => {
    this.preImageList = [];
    this.queryPresDrugback(this.row.presId);
    this.getPrePic(this.row.presId);
    },100)
}
  mounted() {


      
  }
}



